<script>
import instance from '@/axiosInterceptor.js'
import { mapMutations } from 'vuex';
export default {
    name: '',
    components: {},
    data() {
        return {
             theme_img : "",
             branchName : "",  
             themeName : "",
             slotDate : "",
             booker_name: '',
             phone_number: '',
             numUsers : '',

             payment_Method: 'onSite',
             method_radio_options: [
                { item : 'onSite' , name: ''},
                { item : 'card' , name: '', notEnabled: true},
                { item : 'accountTransfer' , name: '', notEnabled: true} 
             ],
             deposit_price:  '',
             totalPrice : '',

             paymentObject: { },

             depositAccount_id: '',
             deposit_account_list : [ ],
            
             depositor_name: '',
             conditions_agree : false,

             slotId : '',
             formatted_date : '',

             themeId : '',
             branchId : '',
             paymentId : '',

       };
    },
    setup() {},
    watch : {
        payment_Method(methodName){
            if(methodName == this.method_radio_options[0].item){
                this.paymentObject = {
                    type: "",
                    price: this.deposit_price,
                }
            }else{
                this.paymentObject = {
                    type: "",
                    price: this.totalPrice
                }
            }
        }
    },
    created() {},
    mounted() {
        this.getThemeInfo();
        this.getBranchInfo();
        this.getSlotInfo();
        this.getBookerInfo();
        this.getBankAccountInfo();
        this.paymentObject = { type: "", price: this.deposit_price } 
    },
    unmounted() {},
    methods: {
        ...mapMutations(['alert_Warning', 'alert_Error', 'alert_Error_And_toHome']),

        getThemeInfo(){
            let theme = this.$store.state.selectedThemeInfo;
            this.themeName = theme.name;
            this.theme_img = theme.themeImg;
            this.themeId = theme.id;
        },
        getBranchInfo(){
            let branch = this.$store.state.selectedBranchInfo;
            this.branchName = branch.name;
            this.branchId = branch.id;
            this.deposit_price = branch.depositPrice;
        },
        getSlotInfo(){
            let slot = this.$store.state.selectedSlotInfo;
            let formatted_slotTime = slot.alterdSlotTime;
            this.formatted_date =  this.dateFormatting(slot.slotDate);
            this.slotDate = this.formatted_date+"\u00A0"+formatted_slotTime
            this.slotId = slot.id;
        },
        getBookerInfo(){
            this.booker_name = this.$store.state.booker_name;
            this.phone_number = this.$store.state.phone_number;
            this.numUsers = this.$store.state.capacityAndPrice.capacity;
            this.totalPrice = this.$store.state.capacityAndPrice.price
        },
        dateFormatting(slotDate){
            let date = slotDate.split('-')
            let year = date[0].substring(2)
            let month = date[1].substring(1)
            let day = date[2]
            return year+""+'\u00A0'+month+""+'\u00A0'+day+''
        },
        getBankAccountInfo(){
            this.$axios({
                url: 'bank',
                method: 'get',
                responseType: 'json',
                params: {
                    branchId : this.$store.state.selectedBranchInfo.id
                }
            }).then((response)=>{
                
                for(let i in response.data.list){
                    let object ={
                        id : response.data.list[i].id,
                        account :  this.banckAccountFormattion(
                            response.data.list[i].bankName,
                            response.data.list[i].bankAccountNumber,
                            response.data.list[i].bankAccountHolder
                    )}
                    this.deposit_account_list.push(new Object(object))
                }
            });
        },
        banckAccountFormattion(name,number,holder){
            //  1111-1111-11-11 (: )
            let formatted = name+"\u00A0"+number+"\u00A0"+"("+holder+")";
            return formatted;
        },
        //  
        numberWithCommas(price){
            return price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        },

        //     
        isItemInputAndSeleted(){
            if(!this.checkItems(this.depositor_name, "   .")
                || !this.checkItems(this.depositAccount_id, "  .") 
                ||!this.checkItems(this.conditions_agree, "        .")){
                return 
            }else{
                return true
            }
        },
        checkItems(item, message){
            if(!item){
                this.alert_Warning(message)
                return false
            }else{
                return true
            }
        },
        //    
        requestToPay(){
            if(this.payment_Method == 'onSite') // 
                //  
                this.$axios({
                    url: '',
                    method: 'post',
                    data: {
                        depositorName : this.depositor_name,
                        depositAccount : this.deposit_account,
                        depositPrice : this.deposit_price
                    }
                }).then((response)=>{
                    //    
                    //     . ->  
                    // callback   . ->      . 
                });
        },

        //axios  +   
        async saveReservation(){

            let endPoint = false

            if(this.payment_Method == 'card'){ // 
                //1. pg  url  + callback url()  
                //2. url 
                //3.
                //4.pg     callback url 
                //5.     
                return this.alert_Warning("    .")


             }else if(this.payment_Method == 'onSite'){ // 
                //  
                await this.$axios({
                    url: 'payment',
                    method: 'post',
                    data: {
                        paymentMethod : this.payment_Method,
                        totPrice : this.totalPrice,
                        depositorName : this.depositor_name,
                        depositPrice : this.deposit_price,
                        bankAccountId : this.depositAccount_id
                    }
                }).then((response)=>{
                        // id
                        // console.log(response)
                        this.paymentId = response.data.data.id
                        console.log("payId = "+this.paymentId)
                });
                //  
                if(sessionStorage.getItem("AccessToken")){  //  
                    await instance({
                        url: 'reservations',
                        method: 'post',
                        data: {
                            bookerName : this.booker_name,
                            slotId : this.slotId,
                            branchId : this.branchId,
                            themeId : this.themeId,
                            numUsers : this.numUsers,
                            privacyAgree : this.$store.state.privacy_agree,
                            conditionsAgree : this.conditions_agree,
                            paymentId : this.paymentId,
                            phoneNum : this.phone_number
                        }

                    })
                    .then((response)=>{
                        // console.log(response)
                        if(response.data.code != 0){
                            this.alert_Error_And_toHome(response.data.message)
                            return
                        }
                        if(response.data.data){
                            endPoint = true
                            this.$store.commit("setReservationInfo",response.data.data)
                            // console.log(this.$store.state.responseReservationInfo)
                        }
                        
                        
                    })
                    .catch((error)=>{
                        this.alert_Error_And_toHome(error.data.message)
                    })
                }else{ //x o
                    await this.$axios({
                        url: 'reservations',
                        method: 'post',
                        data: {
                            bookerName : this.booker_name,
                            slotId : this.slotId,
                            branchId : this.branchId,
                            themeId : this.themeId,
                            numUsers : this.numUsers,
                            privacyAgree : this.$store.state.privacy_agree,
                            conditionsAgree : this.conditions_agree,
                            paymentId : this.paymentId,
                            phoneNum : this.phone_number
                        }

                    })
                    .then((response)=>{
                        // console.log(response)
                         if(response.data.code != 0){
                            this.alert_Error_And_toHome(response.data.message)
                            return
                        }
                        if(response.data.data){
                            endPoint = true
                            this.$store.commit("setReservationInfo",response.data.data)
                            // console.log(this.$store.state.responseReservationInfo)
                        }
                       
                    })
                    .catch((error)=>{
                        this.alert_Error_And_toHome(error.data.message)
                    })
                }
                return endPoint  
            }
        },
      
        

        
    }

}

</script>
