import axios from 'axios'
import {store} from './store'
import router from './router/index'

const instance = axios.create({
  baseURL : process.env.VUE_APP_API_URL
});



instance.interceptors.request.use(
  async function(config){ //   
    config.headers = {
      "AccessToken" : sessionStorage.getItem("AccessToken")
    }
    return config;
  },
  function(error){  //  
    return Promise.reject(error);
  }

)

instance.interceptors.response.use(
  // http status 200  
  async function(resp){ 
    const originalRequest = resp.config
    const { code } = resp.data;

    //AccessToken  ,    ->  
    if(code === -1002 || code === -999){ 
      console.log("    ")
      store.commit('setIsLogin', false)
      store.commit('alert_Error', " .")
      router.push({path: '/'}).catch(()=>{})
      

    //AccessToken    
    }else if(code === -1000){ 
      console.log("AccessToken  ")
      var vm = this
      //RefreshToken   
       await vm.$axios({
        method:"post",
        url: "reissue",
        data:{
          refreshToken : sessionStorage.getItem("RefreshToken")
        }
      }).then(async(response)=>{
        if(response.data.success == true){ 
          console.log("AccessToken & RefreshToekn ")
          // AccessToken & RefreshToekn   -> sessionStorage  
          sessionStorage.setItem("AccessToken", response.data.data.accessToken);
          sessionStorage.setItem("RefreshToken", response.data.data.refreshToken);
           //    ,        
           let instanceConfig = {
              url: originalRequest.url,
              method: originalRequest.method,
           }
           if(originalRequest.data){
               instanceConfig.data = JSON.parse(originalRequest.data)
           }else if(originalRequest.params){
                instanceConfig.params = JSON.parse(originalRequest.params)
           }
          await instance( instanceConfig ).then((response)=>{ 
            resp = response 
          });
            
          return resp;  

        }else{
          //"RefreshToken  "
          console.log("RefreshToekn   ")
          sessionStorage.clear("AceessToken");
          sessionStorage.clear("RefreshStorage");
          store.commit('setIsLogin', false)
          store.commit('alert_Error', "  .");
          return router.push({path: '/'}).catch(()=>{})
        } 
      });

    }else if(code === -1003 || code === 1106){ 
      console.log("RefreshToekn ")
      //RefreshToken   -> localStorage   
      sessionStorage.clear("AceessToken");
      sessionStorage.clear("RefreshStorage");
      store.commit('setIsLogin', false)
      store.commit('alert_Error', "  .");
      router.push({path: '/'}).catch(()=>{})
    }

    return resp;
  },





  function(error){ //http status 200   (catch )
    
    console.log(error);
  
    return Promise.reject(error)
  }

);
//  
export default instance